
import {mapGetters} from 'vuex'
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Calendar from 'vux/src/components/calendar/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import '../assets/js/lrz.bundle'


export default {
computed: mapGetters([]),
components: {
XHeader,
XInput,
Cell,
Group,
Calendar,
Box,
XButton,
XTextarea
},
data() {
return {
insertRecords: {
"loginUserID": "huileyou",
"loginUserPass": "123",
"data": {
"ts_ai_InspectionTime": "",
"ts_ai_Item": "",
"ts_ai_ProjectPro": "",
"ts_ai_Status": "",
"ts_ai_ProcessingStatus": "",
"ts_ai_Inspector": "",
"ts_ai_Construction": "",
"ts_ai_Supervision": "",
"ts_ai_Engineering": "",
"ts_ai_ConstructionTeam": "",
"ts_ai_Picture": ""
}
},
readonly: false,
isShowLoading: false,
ImageURL: [],
fileData: ''
}
},
methods: {


uploadImg(file) {
return new Promise(function (relove, reject) {
lrz(file)
.then(data => {
relove(data.base64.split(',')[1])
})
.catch(function (err) {

alert(err)
})
.always(function () {

});
})
},

uploaNode() {
setTimeout(() => {
if (this.$refs.upload) {
this.$refs.upload.addEventListener('change', data => {
for (var i = 0; i < this.$refs.upload.files.length; i++) {
this.uploadImg(this.$refs.upload.files[i])
.then(data => {
this.$store.dispatch('uploadAdminImgs', {
imageData: data
})
.then(data => {
if (data) {
this.ImageURL.push(data.data);
if (this.ImageURL != '') {
this.imgArr = '';
}
} else {
this.$notify({
message: '图片地址不存在!',
type: 'error'
});
}
})
})
}
})
}
}, 30)
},

addSubmit() {
this.insertRecords.data.ts_ai_Picture = this.ImageURL.join(',');
this.$store.dispatch('addCheckRecordSubmit', this.insertRecords)
.then(() => {
this.$router.push({name: 'InspectProject'})
})
},

goTop() {
this.$router.push({name: 'InspectProject'})
}
},
created() {
this.uploaNode();
this.insertRecords.data.ts_ai_Item = JSON.parse(sessionStorage.getItem('ItemId'));
this.insertRecords.data.ts_ai_ConstructionTeam = JSON.parse(sessionStorage.getItem('TeamName'));
}
}



